/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 90%;
  margin: 0 auto;
  max-width: 52rem;
}
@media (min-width: 769px) {
  .contenedor {
    max-width: max-content;
  }
}

body {
  transition: all 0.3s ease;
  background-color: hsl(226deg, 43%, 10%);
  font-family: "Rubik", sans-serif;
}
@media (min-width: 769px) {
  body main {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
}
body h1, body h2, body h3, body p, body a {
  margin: 0;
  padding: 0;
  font-weight: 300;
}
body h1 {
  font-size: 2.2rem;
}
body h2 {
  font-size: 1.8rem;
}
body a {
  text-decoration: none;
}

