.main-card {
  display: flex;
  flex-direction: column;
  padding: 2rem 2rem 0 2rem;
  margin-top: 2rem;
  border-radius: 2rem;
  position: relative;
}
@media (min-width: 769px) {
  .main-card {
    margin: 0 auto;
    max-width: 20rem;
  }
}
.main-card .user {
  position: relative;
  z-index: 1;
  display: inherit;
  background-color: hsl(246deg, 80%, 60%);
  padding: 2rem;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 2rem;
  gap: 2rem;
}
@media (min-width: 769px) {
  .main-card .user {
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 8rem;
  }
}
.main-card .user__info {
  color: #fff;
  letter-spacing: 0.5px;
}
@media (min-width: 769px) {
  .main-card .user__info h1 {
    font-size: 3.2rem;
    font-weight: 300;
  }
}
.main-card .user__info p {
  color: hsl(236deg, 100%, 87%);
  font-size: 1.4rem;
  margin-bottom: 0.25rem;
}
.main-card .user__img {
  max-width: 100%;
  height: 8rem;
  border: solid 0.35rem #fff;
  border-radius: 50%;
}
.main-card .periods {
  display: inherit;
  flex-direction: row;
  justify-content: space-around;
  position: inherit;
  z-index: 0;
  background-color: hsl(235deg, 46%, 20%);
  padding: 2rem;
  border-radius: 2rem;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  padding-top: 3rem;
  bottom: 1.5rem;
}
@media (min-width: 769px) {
  .main-card .periods {
    flex-direction: column;
    gap: 1rem;
  }
}
.main-card .periods__btn {
  font-size: 1.8rem;
  color: hsl(236deg, 100%, 87%);
}
.main-card .periods__btn.selected {
  transition: all 0.2s ease;
  color: #fff;
}
.main-card .periods__btn:hover {
  color: #fff;
  transition: all 0.3s ease;
}

#cards {
  padding: 0 2rem;
  margin: 0 auto;
}
@media (min-width: 769px) {
  #cards {
    padding: 4rem;
    max-height: 100vh;
    width: 110rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.card {
  position: relative;
  z-index: 1;
  background-color: hsl(235deg, 46%, 20%);
  padding: 2.5rem;
  margin-bottom: 2rem;
  border-radius: 2rem;
}
.card__bg--work {
  display: flex;
  position: relative;
  z-index: 0;
  top: 3rem;
  border-radius: 2rem;
  justify-content: flex-end;
  padding-right: 1rem;
  padding-bottom: 1rem;
  background-color: hsl(15deg, 100%, 70%);
}
.card__bg--play {
  display: flex;
  position: relative;
  z-index: 0;
  top: 3rem;
  border-radius: 2rem;
  justify-content: flex-end;
  padding-right: 1rem;
  padding-bottom: 1rem;
  background-color: hsl(195deg, 74%, 62%);
}
.card__bg--study {
  display: flex;
  position: relative;
  z-index: 0;
  top: 3rem;
  border-radius: 2rem;
  justify-content: flex-end;
  padding-right: 1rem;
  padding-bottom: 1rem;
  background-color: hsl(348deg, 100%, 68%);
}
.card__bg--exercise {
  display: flex;
  position: relative;
  z-index: 0;
  top: 3rem;
  border-radius: 2rem;
  justify-content: flex-end;
  padding-right: 1rem;
  padding-bottom: 2rem;
  background-color: hsl(145deg, 58%, 55%);
  max-height: 7.7rem;
}
.card__bg--social {
  display: flex;
  position: relative;
  z-index: 0;
  top: 3rem;
  border-radius: 2rem;
  justify-content: flex-end;
  padding-right: 1rem;
  background-color: hsl(264deg, 64%, 52%);
  max-height: 7.7rem;
}
.card__bg--social img {
  position: relative;
  overflow: hidden;
}
.card__bg--self-care {
  display: flex;
  position: relative;
  z-index: 0;
  top: 3rem;
  border-radius: 2rem;
  justify-content: flex-end;
  padding-right: 1rem;
  padding-bottom: 1rem;
  background-color: hsl(43deg, 84%, 65%);
}
.card__head-info {
  transition: all 0.5s ease-in;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
}
.card__head-info h2 {
  color: #fff;
  font-size: 1.8rem;
  font-weight: 400;
}
.card__head-info img {
  width: 2rem;
  max-height: 5px;
}
.card__lower-info {
  transition: all 0.5s ease-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card__lower-info .current {
  color: #fff;
  padding: 1rem 0;
  font-size: 3.6rem;
}
.card__lower-info .previous {
  font-size: 1.6rem;
  color: hsl(236deg, 100%, 87%);
}